<template>
  <V-Scroll :isList="false" :scroll-height="`${mainHeight}px`">
    <div class="releaseWin-box">
      <div class="win-tag"></div>
      <span class="tag-title">发布成功</span>
      <span class="win-info">感谢你发布宝贝，小享会尽快审核哒~</span>
      <div class="btns">
        <button class="btn">查看详情</button>
        <button class="btn btn-red">分享宝贝</button>
      </div>
      <div class="goHome">
        <span class="icon-home"></span>
        <span class="text">返回首页</span>
      </div>
    </div>
  </V-Scroll>
</template>

<script>
import Ajax from "@/mixins/ajax";

export default {
  name: "releaseWin",
  mixins: [Ajax]
};
</script>

<style scoped>
.releaseWin-box {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 1.8rem;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 0.4rem;
}
.win-tag {
  width: 2.93rem;
  height: 3.67rem;
  background: url("../../images/releaseGoods/win.png") no-repeat;
  background-size: 100%;
  margin-bottom: 1.04rem;
}
.tag-title {
  font-size: 0.72rem;
  color: #0b8942;
  margin-bottom: 0.31rem;
}
.win-info {
  font-size: 0.42rem;
  color: #888;
  margin-bottom: 1.67rem;
}
.btns {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.btns .btn {
  width: 4.05rem;
	height: 1.32rem;
	background-color: #fff;
	border-radius: 0.66rem;
  border: solid 0.03rem #de2245;
  color: #de2245;
  font-size: 0.54rem;
}
.btns .btn.btn-red {
  background-color: #de2245;
  color: #fff;
}
.goHome {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.2rem;
}
.goHome .icon-home {
  width: 0.57rem;
	height: 0.57rem;
  background: url("../../images/releaseGoods/icon-home.png") no-repeat;
  background-size: 100%;
}
.goHome .text {
  color: #de2245;
  font-size: 0.48rem;
  margin-left: 0.2rem;
}
</style>



